 * @Description: In User Settings Edit
 * @FilePath: \工作项目协同开发\operation_system\src\components\shopManagement\brandmaintenance\BrandMaintenance.vue
-->
<template>
  <div id="ipi-board">
    <div class="left">
      <div class="title">
        <h3 style="text-align:center">品牌维护-备案文档管理</h3>
      </div>
      <div class="left-content">
        <h4>请选择店铺</h4>
        <!-- 菜单 -->
        <el-menu
          default-active="0-0-0"
          class="el-menu-vertical-demo"
          unique-opened
        >
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div class="upload">
        <span style="margin-right:20px;">点击上传品牌备案文件</span>
        <el-button type="primary" size="mini" @click="uploadClick"
          >上传</el-button
        >
      </div>
      <div class="table">
        <el-table
          :data="brandFileList"
          border
          style="width: 100%"
          max-height="700"
          ref="bmTable"
        >
          <el-table-column
            type="index"
            label="序号"
            width="70"
          ></el-table-column>
          <el-table-column
            prop="1"
            label="渠道"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="2"
            label="店铺"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="3"
            label="站点"
            width="70"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="4"
            label="国家"
            width="70"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="5"
            label="品牌"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="6"
            label="品牌备案文件"
            width="300"
            align="center"
          >
            <template slot-scope="scope">
              <a :href="_urlzgl + scope.row[6]" target="_blank">{{
                scope.row[6].slice(scope.row[6].lastIndexOf("/") + 1)
              }}</a>
            </template>
          </el-table-column>
          <el-table-column
            prop="7"
            label="备案人"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="8"
            align="center"
            label="备案时间"
          ></el-table-column>
        </el-table>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total,prev, pager, next, jumper"
          :total="total"
          style="text-align:center;margin-top: 10px;"
        ></el-pagination>
      </div>
    </div>

    <!-- 上传对话框 -->
    <el-dialog
      :visible.sync="uploadBrandDialogVisible"
      width="30%"
      center
      @close="uploadBrandDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      title="备案文件上传"
      class="upload-dialog"
    >
      <!-- 表单区域 -->
      <el-form
        :model="uploadBrandForm"
        :rules="formRules"
        ref="uploadBrandRef"
        label-width="100px"
      >
        <el-form-item label="渠道" prop="platform">
          <el-select v-model="uploadBrandForm.platform" placeholder="请选择">
            <el-option
              v-for="(item, index) in platformList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家" prop="country">
          <el-select
            v-model="uploadBrandForm.country"
            placeholder="请选择"
            @change="selectChange"
          >
            <el-option
              v-for="(item, index) in countryList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点" prop="site">
          <el-select v-model="uploadBrandForm.site" placeholder="请选择">
            <el-option
              v-for="(item, index) in siteList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="店铺" prop="store">
          <el-select v-model="uploadBrandForm.store" placeholder="请选择">
            <el-option
              v-for="(item, index) in storeList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="品牌" prop="name">
          <el-input v-model="uploadBrandForm.name"></el-input>
        </el-form-item>
        <el-upload
          class="upload-demo"
          drag
          action="123"
          multiple
          :before-upload="beforeUpload"
          :http-request="uploadFile"
          ref="upload"
          :on-remove="handleRemove"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将文件拖到此处，或
            <em>点击上传</em>
          </div>
          <!-- <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div> -->
        </el-upload>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="uploadBrandDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="uploadBrandConfirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",

      // 品牌备案文件列表
      brandFileList: [],
      // 上传文件对话框
      uploadBrandDialogVisible: false,
      uploadBrandForm: {
        platform: "",
        country: "",
        site: "",
        store: "",
        name: ""
      },
      // 选中的文件数量
      fileData: [],
      // 上传的文件
      file: "",
      // 上传对话框 下拉框数据
      platformList: ["Amanzon"],
      countryList: [],
      siteList: [],
      storeList: [],

      // 验证表单
      formRules: {
        platform: [
          { required: true, message: "请选择渠道", trigger: "change" }
        ],
        site: [{ required: true, message: "请选择站点", trigger: "change" }],
        country: [{ required: true, message: "请选择国家", trigger: "change" }],
        store: [{ required: true, message: "请选择店铺", trigger: "change" }],
        name: [{ required: true, message: "请输入品牌", trigger: "blur" }]
      },
      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 50
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.data1;
        this.menuCountryList = res.data.data2;
        this.menuSiteList = res.data.data3;

        // 页面刷新 先默认展示京汇的 同时在导航那里添加default-active="0-0-1"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    // 获取国家下拉框列表
    getCountryList() {
      this.$api.getCountryList().then(res => {
        console.log(res);
        this.countryList = res.data.data;
      });
    },
    // 获取上传中 店铺 的下拉框数据
    getSelectData() {
      this.$api.getSelectData().then(res => {
        this.storeList = res.data.store_list;
        // 去重
        this.storeList = [...new Set(this.storeList)];
      });
    },
    onceClick(index) {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      // console.log(site);
      let page = 1;
      this.$api.queryBrand(platform, country, site, page).then(res => {
        console.log(res);
        this.brandFileList = res.data.data;
        this.total = res.data.all_number;
        this.$nextTick(() => {
          this.$refs["bmTable"].bodyWrapper.scrollTop = 0;
        });
      });
    },
    // 上传品牌文件相关事件
    uploadClick() {
      this.getCountryList();
      this.getSelectData();
      //点击
      this.uploadBrandDialogVisible = true;
    },
    uploadBrandConfirm() {
      //确认上传
      this.$refs.uploadBrandRef.validate(state => {
        if (!state) return;
        if (this.fileData.length == 0) {
          this.$message.closeAll();
          return this.$message.error("请上传至少一个文件");
        }
        let formData = new FormData();
        formData.append("brand_platform", this.uploadBrandForm.platform);
        formData.append("brand_country", this.uploadBrandForm.country);
        formData.append("brand_site", this.uploadBrandForm.site);
        formData.append("brand_store", this.uploadBrandForm.store);
        formData.append("brand_name", this.uploadBrandForm.name);
        // formData.append('file',this.file);
        for (let item of this.fileData) {
          formData.append("file", item);
        }
        // formData.append('file',this.fileData);

        // let config = {
        //   headers: {'Content-Type': 'multipart/form-data'}
        // }
        this.$api.uploadBrand(formData).then(res => {
          // console.log(res);
          this.$message.success("上传成功");
          this.thirdClick(
            this.checkedPlatform,
            this.checkedCountry,
            this.checkedSite
          );
          this.uploadBrandDialogVisible = false;
        });
      });
    },
    uploadBrandDialogClose() {
      this.$refs.uploadBrandRef.resetFields();
      this.$refs.upload.clearFiles();
    },
    // 下拉框联合
    selectChange(val) {
      console.log(val);
      this.$api.getSiteList(val).then(res => {
        console.log(res);
        this.siteList = res.data.data;
      });
    },
    beforeUpload(file) {
      //文件上传前发生
      // console.log(file);
      this.fileData.push(file);
      this.file = file;
    },
    uploadFile(file) {
      // console.log(file.file);
      // this.fileData.push(file.file);
    },
    // 文件移除时触发
    handleRemove(file) {
      for (let i in this.fileData) {
        if (this.fileData[i] === file.raw) {
          this.fileData.splice(i, 1);
        }
      }
    },
    // 分页相关
    handleCurrentChange(val) {
      // 切换页面滚动到顶部 bmTable

      console.log(val);
      this.$api
        .queryBrand(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          val
        )
        .then(res => {
          this.brandFileList = res.data.data;
          this.$nextTick(() => {
            this.$refs["bmTable"].bodyWrapper.scrollTop = 0;
          });
        });
    }
  },
  created() {
    // this.getSelectData();

    this.getMenuList();
  },
  mounted() {}
};
</script>
<style scoped>
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
}
#ipi-board >>> input[type="file"] {
  display: none;
}
#ipi-board .el-table .cell {
  padding-right: 0;
  padding-left: 0;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.title {
  margin-top: 2vh;
}
.right {
  position: absolute;
  top: 0;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  left: 18vw;
  padding: 24px;
  overflow: auto;
}
.left-content h4 {
  margin-top: 2vh;
  text-align: center;
}
.upload {
  margin-bottom: 24px;
  display: flex;
}
.upload-dialog .el-select,
.upload-dialog .el-input {
  width: 14vw;
}
.upload-dialog >>> .el-upload,
.upload-dialog >>> .el-upload-dragger {
  width: 100%;
}
</style>



